        <h3 ref="sintesis">Sntesis</h3>
        <p>En el presente informe se estudia la implantacin del fenmeno de los locales de juego y los salones de apuestas en el municipio de Madrid. stos, junto con las apuestas en Internet, han tenido en los ltimos aos, y siguen teniendo, un fuerte incremento, lo cual ha llevado a que grandes fondos de inversin internacionales estn tomando posiciones en el sector.</p>
        <p>Ese crecimiento, sin embargo, constituye un problema social importante dadas las consecuencias que entraa para la economa de las familias y para la salud colectiva. La ludopata es, sin duda, una patologa social que no cesa de crecer y enraizar en las grandes ciudades, teniendo a la juventud como uno de sus rehenes favoritos.</p>
        <p>Los poderes pblicos apenas estn reaccionando, incluso se hallan casos de administraciones que no admiten que exista un problema al respecto. Se da un parecido con la emergencia de las viviendas de uso turstico en los centros de las grandes urbes. Proliferan sin que nadie se fije. Luego, cuando se quiere hacer algo, regular, resulta que es tarde y todo son inconvenientes.</p>
        <p>En el lado opuesto, vemos cmo en los barrios ms modestos prende cada vez ms la queja ciudadana contra el crecimiento descontrolado de los locales de juego y los salones de apuestas, unida a la demanda de medidas reguladoras.</p>
        <p>En las pginas que siguen nos detenemos en examinar el nmero y, sobre todo, la correlacin del nmero con otras variables socioeconmicas. La hiptesis principal es que las casas de juego no se reparten al azar sobre el territorio, sino que la localizacin se organiza sobre patrones bien identificables.</p>
        <p>En julio de 2019, segn el Censo de Locales del Ayuntamiento de Madrid, haba en el municipio 378 locales de juegos de azar, sin contar las administraciones de lotera y actividades sujetas a autorizacin especial. En el trabajo de campo realizado por las asociaciones vecinales con motivo de este informe se ha constatado que, de aqullos, varios no existen, pero, a la vez, se han detectado otros que operan con licencias de actividad distintas, en su mayora como administraciones de lotera y como establecimientos de entretenimiento. Todo ello nos ha permitido elevar la cantidad inicial hasta un universo de 400 locales. De ellos, en el 75% estn presentes Sportium y Codere, definiendo, de tal modo, una estructura de mercado de duopolio. Paradjicamente esto no ha despertado el celo de la Comisin Nacional de los Mercados y la Competencia, tan pronta a intervenir cuando los ayuntamientos y las CC AA intentan poner freno a determinadas prcticas econmicas llamadas colaborativas.</p>
        <p>Las casas de juego, preferentemente, se hallan dentro de la margen occidental de la almendra central y en los barrios perifricos del sur prximos a la M-30. En posiciones perifricas hay pocas. En particular, se identifican fuertes aglomeraciones en Aluche-Vista Alegre-San Isidro, Moscard-Almendrales, San Diego-Numancia, Sol, Bellas Vistas y Quintana. Ms en concreto, en esos espacios sobresalen determinadas calles y sus entornos: Bravo Murillo, Alcal, Marcelo Usera, General Ricardos, avda. de la Albufera Se advierte una muy clara vinculacin en la implantacin de los ejes comerciales de carcter distrital y reas de gran afluencia de pblico. A ellos se suma el espacio singular delimitado por la Puerta del Sol y sus alrededores.</p>
        <p>La puesta en relacin de la distribucin territorial de los establecimientos y las caractersticas socioeconmicas de los barrios de Madrid aporta, junto a la observacin anterior, pistas muy trascendentes acerca del patrn de implantacin del juego. Atendiendo a las caractersticas de los barrios en los que se instalan, las notas que merece la pena subrayar son:</p>
        <ul>
          <li>Predominan los barrios en los que la renta media de los hogares est por debajo de la media municipal. De ah la acusada presencia en la zona sur, junto a Tetun y Quintana. En cambio, se observa que hay muy pocos locales en los barrios que lindan con La Castellana u otros como Las Palomas, La Piovera, El Planto</li>
          <li>La situacin se repite cuando la distribucin de los locales se compara con otras variables que, de un modo u otro, revelan el estatus del mbito. As, hay pocos locales en los barrios en que la poblacin adulta tienen un alto nivel de estudios acabados y, al revs, hay muchos en los barrios en los que el nivel de estudios es bajo.</li>
          <li>Los mismo ocurre a propsito de la distribucin de la tasa de desempleo por barrios.</li>
          <li>En otro orden de cosas, se tiene que los establecimientos se dan ms en barrios con mucha poblacin y altas densidades. Parece, as pues, que buscan la proximidad de un tejido urbano muy consolidado, con actividad, saturado.</li>
          <li>Tambin se aprecia que es ms probable encontrar locales en los barrios en los que el peso porcentual de la juventud es mayor, si bien aqu la correlacin es muy dbil.</li>
          <li>Esa correlacin, en cambio, es muy notable en los barrios en los que el peso de la poblacin inmigrante extranjera es elevado. Tal sucede con Bellas Vistas, Quintana, Moscard, Almendrales, Vista Alegre</li>
          <li>En fin, son barrios con precios de las viviendas -euros por metro cuadrado- ms econmicos que en el resto de la ciudad, con la excepcin de los ms cntricos (distritos de Centro, Chamber y, en parte, Tetun).</li>
        </ul>
        <p>La conjugacin de este conjunto de variables, entre otras, es el que determina las pautas de implantacin. De tal modo, concluyendo, parece que los locales buscan zonas en las que la localizacin sea ms barata y, al tiempo, zonas en las que la poblacin residente ofrece un nivel adquisitivo inferior a la media y un estatus social menos privilegiado, si bien por encima de los barrios ms vulnerables.</p>
        <p>Hoy por hoy, los locales conforman una dotacin con una cobertura territorial limitada. No son negocios de proximidad en sentido estricto. La lgica de implantacin no es la de, por as decirlo, una tienda de barrio.</p>
        <p>As las cosas, hemos de guardarnos de conclusiones precipitadas. El fenmeno de las casas de juego es reciente, probablemente no ha tocado techo. Las pautas de localizacin ahora conocidas dan pistas sobre el futuro previsible.</p>
        <p>As, encontramos muchos barrios con un perfil socioeconmico y demogrfico parecido al de los barrios con muchos locales que, sin embargo, carecen de ellos o que a lo sumo alojan 1-2.</p>
