        <p>Si centramos la atencin -por razones obvias- en los centros pblicos y privados en los que se imparte el nivel de secundaria, tenemos que casi la totalidad de los establecimientos de juego estn respecto de ellos a una distancia inferior a los 500 metros. La proximidad define una situacin de riesgo frente a la que es imperativo establecer normas de proteccin preventiva.</p>
