        <!-- start intro -->
        <h3 ref="introduccion">Introduccin</h3>
        <p>El objeto del presente estudio es la distribucin espacial de los establecimientos de juegos y apuestas existentes en la ciudad de Madrid, en la amplia acepcin que recoge el artculo 1 de la <a href="http://www.madrid.org/wleg_pub/secure/normativas/contenidoNormativa.jsf?opcion=VerHtml&nmnorma=349&cdestado=P#no-back-button" target="_blank">Ley 6/2001, del juego en la Comunidad de Madrid</a>: locales en los que se realizan actividades en las que se arriesguen cantidades de dinero u objetos econmicamente evaluables en cualquier forma sobre los resultados, y que permitan su transferencia entre los participantes, con independencia de que predomine en ellos el grado de destreza de los jugadores o sean exclusiva o primordialmente de suerte, envite o azar y tanto si se desarrollan mediante la utilizacin de mquinas automticas como si se llevan a cabo a travs de la realizacin de actividades humanas, cualquiera que sea el medio por el que se realicen.</p>
        <p>Aqu entran los casinos, bingos, locales de juegos, salones de apuestas, establecimientos de juegos colectivos de dinero y azar, etc. Se excluyen las administraciones de lotera, estancos, ONCE y, en general, los espacios de juegos de gestin pblica o sometidos a autorizacin especial. Tambin quedan fuera los bares, restaurantes y lugares con unas pocas mquinas tragaperras y similares. Solo se consideran los negocios estables, no se contabilizan las instalaciones temporales o efmeras.</p>
        <p>Para establecer el universo de estudio se ha partido del listado de establecimientos con el epgrafe 920002 en el Censo de Locales del Ayuntamiento de Madrid, que comprende los juegos de azar y apuestas de gestin privada. En el trabajo de campo se han visitado todas las direcciones a fin de verificar la actividad. Ello ha permitido depurar el listado eliminando los locales en los que la actividad no se corresponde con el epgrafe o en los que no hay actividad alguna (viviendas, solares). En paralelo, en el trabajo de campo se ha peinado buena parte de la ciudad a fin de identificar aquellos establecimientos en los que se practican juegos de azar y/o apuestas pero que figuran con otro epgrafe en el Censo o incluso que no estn (o no ha sido posible hallarlos) en l. Estas incorporaciones han sido fotografiadas y rastreadas en el Censo a fin de acotar la actividad dada de alta. En muchos casos, como se ver, se corresponden con los epgrafes 920001 (juegos de azar y apuestas de gestin pblica) o 932007 (salones de recreo, diversin y otras actividades recreativas).</p>
        <p>El trabajo de campo se ha realizado en poco tiempo y gracias a la aportacin voluntaria de muchas personas en los 21 distritos, bajo la supervisin de la FRAVM. Esto nos asegura que el universo sobre el que se trabaja aqu es aproximado al real o, dicho de otra manera, todos los establecimientos incluidos son salones de apuestas-locales de juego, pero seguramente han quedado establecimientos sin identificar. Cabe confiar que el nmero de stos no sea grande. En cualquier caso, esta circunstancia obliga a aplicar la cautela en el examen de los datos.</p>
