        <p>En el trabajo de campo se han dado de baja 52 establecimientos. La mayora de ellos bien no han abierto, bien han cerrado la actividad en el ltimo ao. Algunos desarrollan otros negocios (bares, restaurantes) o son las oficinas de empresas de juegos En algn caso son viviendas. En el lado contrario, se han aadido 74 establecimientos. Buena parte de ellos son administraciones de loteras en las que -adems de la actividad propia- se practican otras apuestas-juegos y, asimismo, salones de recreo en los que -adems de actividades recreativas- se realizan apuestas-juegos de azar. En pocos casos, la licencia de actividad es de bares, restaurantes En un nmero reducido de locales no ha sido posible asociarlos a una licencia, sea porque carecen de ella, sea porque operan a partir de una declaracin responsable, sea porque las direcciones de la licencia y la localizacin efectiva no coinciden, sea por otras razones, sin descartar las dificultades del rastreo en campo.</p>
        <p>El universo, por tanto, queda formado por 400 locales y salones. En el cuadro se especifica la titularidad del negocio para los casos en que ha sido posible concretarla. Dos empresas (Codere y Sportium) ejercen un fuerte dominio: sea en solitario, sea asociadas con otros operadores, estn presentes en, al menos, el 75% de los locales. En tercer lugar, pero muy lejos de las anteriores, est Luckia, en la que participan Egasa y Cafeteras Nebraska. Es indiscutible, por tanto, la situacin de duopolio sobre la que se organiza el mercado del juego en la ciudad, tanto ms si se considera que Sportiun es propiedad, el 100%, del fondo de inversin Blackstone y que Codere pertenece a Silver Point, Prudential PLC, Abrams Capital, Contrarian y Martnez Sampedro.</p>

