        <p>Como caba esperar, la mayor concentracin se produce en las calles aledaas a la Puerta del Sol (Montera, Mayor, Carretas, Preciados, Espoz y Mina). Tambin sobresalen la calle Atocha y el entorno de la plaza Mayor. Asimismo, destacan el mbito de las plazas de San Bernardo-Quevedo-Bilbao y el tramo de Bravo Murillo prximo a la plaza de Cuatro Caminos.</p>

        <figure>
          <img src="/static/img/maps/chamberi.jpg" alt="Mapa de locales de Centro-Chamber">
          <figcaption>Mapa de locales de Centro-Chamber. FRAVM.</figcaption>
        </figure>

        <h4 ref="tetuan">Tetun</h4>
        <p>Justo Bravo Murillo y calles aledaas absorben buena parte de los locales situados en Tetun. A ellos se suman algunos en Orense-General Pern. Se observa tambin la concentracin en el entorno de La Vaguada, ya en el distrito de Fuencarral-El Pardo.</p>

        <figure>
          <img src="/static/img/maps/tetuan.jpg" alt="Mapa de locales de Tetun">
          <figcaption>Mapa de locales de Tetun. FRAVM.</figcaption>
        </figure>

        <h4 ref="ciudadlineal">Ciudad Lineal</h4>
        <p>Como en Tetun, gran parte de los locales se localizan en la calle Alcal (tramo entre Ventas y Arturo Soria) y aledaas. Es la zona que ms se aparta del patrn de concentracin dominante. Define el espacio de mayor acumulacin en el este del municipio.</p>

        <figure>
          <img src="/static/img/maps/tetuan.jpg" alt="Mapa de locales de Ciudad Lineal">
          <figcaption>Mapa de locales de Ciudad Lineal. FRAVM.</figcaption>
        </figure>

        <p>En resumen, en los espacios de referencio citados sobresalen los grandes ejes comerciales de distrito, muy consolidados y con radios de influencia amplios, que desbordan incluso los entornos distritales. Tal es el caso de Bravo Murillo, Alcal, General Ricardos o la avda. de la Albufera. En el resto de los distritos, la presencia de locales es menor que en los mbitos que se acaba de describir. En ellos se repite la pauta que se ha destacado: ubicacin en calles-plazas principales y sus proximidades, todas ellas con marcado carcter comercial, bien comunicadas y con afluencia de pblico. As, por ejemplo, Monforte de Lemos, Alberto Palacios, Lpez de Hoyos, Doctor Esquerdo, Francisco Silvela, San Cipriano</p>

        <table-streets-by-number/>

        <p>Un dato digno de subrayar es que todos los grandes intercambiadores de transporte, salvo las terminales aeroportuarias, tienen cerca o muy cerca al menos una casa de juegos.</p>

        <figure>
          <img src="/static/img/maps/intercambiadores.jpg" alt="Mapa de locales e intercambiadores">
          <figcaption>Mapa de locales e intercambiadores. FRAVM.</figcaption>
        </figure>

        <p>En el siguiente mapa se observa, asimismo, que la mayora de los locales de juego y apuestas estn cerca de grandes centros comerciales y/o galeras de alimentacin, si bien no sucede lo mismo al revs, es decir, muchos centros-galeras no tienen cerca un local de juegos.</p>

        <figure>
          <img src="/static/img/maps/centroscomerciales.jpg" alt="Mapa de locales y centros comerciales">
          <figcaption>Mapa de locales y centros comerciales. FRAVM.</figcaption>
        </figure>

        <p>Por ltimo, merece la pena fijarse en la discordancia entre la localizacin de los locales de juego de gestin pblica o sometidos a autorizacin especial (administraciones de lotera) y los locales de gestin privada. Aunque los primeros tambin tienden a instalarse en los ejes comerciales y zonas de gran afluencia de pblico, es claro que ofrecen una pauta de mayor dispersin y ocupacin del espacio edificado.</p>

        <figure>
          <img src="/static/img/maps/gestionpublica.jpg" alt="Mapa de locales de gestin pblica">
          <figcaption>Mapa de locales de gestin pblica (administraciones de lotera). FRAVM.</figcaption>
        </figure>
        <!-- end implantacion -->


        <!-- start determinantes -->
        <h3 ref="determinantes">Determinantes de la localizacin</h3>
        <p>Aparte la relacin fundamental con los ejes comerciales y espacios de aglomeracin, qu otras variables inciden en la localizacin de los locales de juego de gestin privada? La hiptesis por la que nos guiamos se basa en el hecho de que las casas de juego son negocios en el sentido ms estricto del trmino. Como tales, la estrategia de localizacin deber responder a dos fuerzas: por una parte, el acercamiento a la demanda actual y potencial y, por otra parte, la reduccin de los costes de implantacin. En la medida en que ambos se contradigan, se formarn soluciones de compromiso, intermedias.</p>
        <p>En las pginas que siguen se indagan ambos supuestos relacionando la distribucin de los establecimientos por barrios con un abanico de caractersticas socioeconmicas y demogrficas de los mismos. En concreto, se evala la relacin de dependencia bivariada del nmero de locales con dos variables de estatus (la renta media de los hogares y el nivel de estudios de la poblacin de 25 y ms aos), una de actividad econmica (la tasa absoluta de desempleo), cinco demogrficas (poblacin, densidad, peso porcentual de los segmentos de jvenes y de 65 y ms aos y tasa de extranjera) y una geogrfica (distancia de los barrios a la Puerta del Sol).</p>
        <p>En todos ellos se sigue el mismo procedimiento de aproximacin. Se aplica un enfoque doble. Por una parte, el anlisis de contingencia permite establecer si existe asociacin entre las variables y, en su caso, medir el grado o la fuerza de la misma. Por otra parte, los diagramas de dispersin y los grficos sirven para examinar la linealidad de las asociaciones bivariadas. Al final del apartado se completa este enfoque mediante un anlisis de regresin mltiple.</p>

        <h4 ref="rentabarrios">Renta media de los hogares por barrios</h4>

        <p>Sin duda, una de las primeras a estudiar es la renta neta media de los hogares. En la tabla que sigue se cruza el nmero de barrios segn la renta y segn el nmero de locales. La distribucin de la renta est agrupada en cinco intervalos, de los cuales, en el tercero est la media simple de los barrios. Existe una asociacin moderada entre ambas variables, pues se obtiene un valor alto del chi cuadrado (38,2, con una probabilidad muy baja de obtener un valor ms alto, p = 0.005). A su vez, el coeficiente de contingencia, que mide la fuerza de la relacin, es 0.46, sobre un coeficiente mximo posible de 0.89. La gran mayora de los barrios que se hallan por encima de la renta media municipal o no tienen casas de juego o a lo sumo localizan 1 o 2. En cambio, la mayora de los barrios que estn por debajo de la media municipal tienen 3 o ms. Ms de la mitad de los barrios con 3 o ms establecimientos se clasifican en los dos intervalos ms bajos de la escala de rentas. En la segunda tabla se recogen los residuos tipificados corregidos de Haberman. Para un nivel de confianza de 0,95, se puede decir que los residuos mayores de 1,96 sealan los cruces de variables con ms casos observados que los esperados si aqullas fueran independientes o no estuvieran asociadas en una relacin de dependencia y los residuos negativos mayores en valor absoluto de 1,96 apuntan los cruces con menos casos observados que los esperados. Para niveles de confianza inferiores sendos valores seran menores en valor absoluto (por ejemplo, para un nivel de confianza del 0,9, seran +/- 1,64). La interpretacin de los residuos confirma de manera palmaria la asociacin entre las variables y subraya el fuerte protagonismo en la distribucin de las casas de juego de la polaridad barrios con rentas bajas y medio bajas y barrios con rentas medias altas y muy altas.</p>

      </section>
    </div>

