        <table-neighborhoods-by-locals/>
      </section>
    </div>

    <div class="page__wrap">
      <section class="page__section page__section--text">

        <p>En el siguiente mapa se recoge la distribucin de los barrios segn la renta neta media en 2015. La informacin incide en los resultados que se han subrayado arriba. Se observa que la mayora de los barrios con nmeros altos de locales, a su vez, son barrios con rentas netas por hogar residente bajas o medio bajas. Las excepciones se producen en Trafalgar y Argelles, en el distrito de Chamber, y Castillejos, en el de Tetun. En general, en los barrios con rentas medias altas o no hay locales o hay muy pocos. No obstante, entre ambas variables la correlacin es dbil, aunque significativa. La correlacin de Pearson es -0.39, sobre un mximo de -1, esto es, a mayor renta menor frecuencia de locales. Ahora bien, igualmente es cierto que muchos barrios con rentas bajas alojan pocos locales o ninguno, tal como se ve en el grfico de dispersin adjunto. El valor absoluto del coeficiente aumenta algo si nos deshacemos de los barrios ms perifricos, acotando el mapa a la almendra central y a la rbita de barrios perimetrales o prximos a la M-30.</p>

        <figure>
          <img src="/static/img/charts/mapa-renta.jpg" alt="Barrios segn renta">
          <figcaption>Barrios segn renta. FRAVM.</figcaption>
        </figure>
        
        <chart-neighborhoods-by-rent-locals/>

        <p>El diagrama de cajas ilustra perfectamente la situacin. En l se agrupan los barrios en cinco niveles de renta (de menos a ms) y para cada uno se cuenta con la distribucin del nmero de locales, sealando el valor mximo, mnimo, la mediana y los percentiles 25 y 75. A mayor amplitud de la caja, mayor dispersin de la variable. El mximo y la mediana son elevados en los estratos bajos de renta y desciende muy significativamente al pasar de uno al siguiente. Ntese la desemejanza extrema entre el primer y el quinto estrato.</p>

        <figure>
          <img src="/static/img/charts/boxplot-renta.png" alt="Barrios segn renta" style="width:300px; height: auto;">
          <figcaption>Barrios segn renta. FRAVM.</figcaption>
        </figure>

        <p>En el grfico siguiente se representa el promedio no ponderado de los ingresos medios de los hogares de los barrios clasificados segn el nmero de locales de juego. El sentido descendente de la curva es palmario.</p>

        <chart-neighborhoods-by-rent-locals-line/>

        <h4 ref="paro">Tasa absoluta de paro</h4>

        <p>Otra variable a considerar es la tasa de desempleo por barrios: nmero de personas desempleadas/poblacin activa de 16 y ms aos. No se cuenta con ese dato, sino con uno aproximado: la tasa absoluta de paro. Por ella se entiende la proporcin de paro registrado de cada barrio sobre el total de la poblacin (activa ms inactiva) de 16 y ms aos. Se aprecia una asociacin moderada entre la tasa y el nmero de locales: chi cuadrado igual a 36,8, con p = 0.002, y coeficiente de contingencia igual a 0,48 sobre un mximo de 0,89. Se cumple que los barrios con menor intensidad del desempleo tienen pocas casas de juego, mientras que stas se concentran en los barrios con tasas altas y medias.</p>

      </section>
    </div>

