        <p>Las tasas de paro ms altas se producen en los barrios del sur y este de la ciudad, donde tambin se localizan ms locales de juego. La correlacin no es alta, sin embargo, como se observa en el diagrama de dispersin. En ste no se delinea una curva bien definida, sino que la nube de puntos es amorfa. La curva de promedios no ponderados de la tasa de paro muestra un ascenso segn aumenta el nmero de locales hasta 8. Ah baja, para luego volver a subir. Muchos barrios con tasas medias tienen bastantes establecimientos. La misma informacin se observa en el diagrama de cajas.</p>
        
        <figure>
