        <chart-neighborhoods-by-unemployment-locals-line/>
        
        <h4 ref="educacion">Nivel educativo</h4>

        <p>Una variable con acusado poder de discriminacin del estatus social de los barrios es el nivel de estudios acabados de la poblacin residente. La web municipal ofrece ese dato referido a la poblacin de 25 y ms aos. A partir de l se ha elaborado para cada barrio el ndice de proporcin de poblacin de 25 y ms aos analfabeta, sin estudios o solo con estudios primarios. La tabla de contingencia que sigue muestra una leve relacin entre ambas variables, pues el chi cuadrado asciende a 31,1, que es significativo para un valor p = 0.05. El coeficiente de contingencia sube a 0,44, sobre un mximo de 0,89. Se observa que en los barrios en los que la poblacin tiene un nivel educativo elevado hay pocos locales y, al revs, en los barrios con proporciones superiores a la media municipal de habitantes con bajo nivel de estudios hay muchos locales.</p>

      </section>
    </div>

    <div class="page__wrap page__wrap--big">
      <section class="page__section page__section--text">
        <table-neighborhoods-by-studies/>
      </section>
    </div>

    <div class="page__wrap">
      <section class="page__section page__section--text">

        <p>Pero, como ocurre a propsito de las variables ya analizadas, el grado de la asociacin no es fuerte en tanto que tambin hay muchos barrios con bajo nivel de estudios y pocos locales y al revs, barrios con nivel medio o alto y muchos locales. La asociacin bajo nivel-muchos locales es clara a propsito de Latina, Carabanchel, Usera y Puente de Vallecas, no as a propsito de Centro, Chamber y Tetun. A su vez, la asociacin alto nivel-pocos locales es cierta para Salamanca, Chamartn, Moncloa y barrios perifricos de Hortaleza y Fuencarral, no as para Villaverde, Villa de Vallecas, Casco Histrico de Viclvaro o el Gran San Blas. Se repiten aqu grosso modo los resultados que destacamos respecto de las variables renta media y tasa absoluta de paro. A mayor abundamiento, en el ltimo grfico que se inserta a continuacin se aprecia cmo, en particular, son los barrios con 9-10 y 20 locales los que ms rompen la linealidad de la relacin, a saber, entre otros, Sol, Palos de Moguer, Castillejos, Quintana, Gaztambide, Cuatro Caminos Todos ellos, barrios cntricos con niveles educativos en la media o por encima de la media, pero con muchos locales. </p>

