        <h4 ref="distanciasol">Distancia de los barrios a la Puerta del Sol</h4>

        <p>Las relaciones que se han analizado hasta aqu sugieren la influencia de una cuarta variable: la menor-mayor centralidad de los barrios. Para estudiarla se ha medido la distancia de cada barrio a la Puerta del Sol: en concreto, se ha medido la distancia en lnea recta de sta a los centroides de cada barrio, segn los determina QGIS. En el siguiente mapa se han clasificado los barrios en cinco coronas segn la distancia al centro y sobre ellos se impresionan las casas de juego segn su localizacin. Se observa que la mayora caen dentro de la primera y la segunda corona, es decir, en los barrios ms cntricos. An hay un nmero significativo en la tercera, En la cuarta y quinta, en cambio, hay muy pocos. Buena parte de los establecimientos estn dentro de un radio de 6 Km alrededor de la Puerta del Sol. Supngase que el origen de coordenadas cartesianas se coloca en la plaza de Coln. La mayora de las casas caen al sur (eje de Carranza-Sagasta-Goya) y al oeste (eje de La Castellana-paseo de Recoletos-El Prado).</p>

        <figure>
          <img src="/static/img/charts/mapa-distancia.jpg" alt="Barrios segn distancia a la Puerta del Sol">
          <figcaption>Barrios segn distancia a la Puerta del Sol. FRAVM.</figcaption>
        </figure>

        <p>El anlisis de contingencia repite los resultados hasta ahora obtenidos: la relacin de dependencia (distancia-locales) no es significativa, (chi cuadrado = 23.5, con p = 0.102), el coeficiente de contingencia es moderado (0,39 sobre un mximo de 0,89, pero con una p demasiado alta). Sucede que, si bien la mayora de los locales se localizan en las dos primeras coronas, en ellas, asimismo, estn muchos barrios sin o con muy pocos locales. Aqu la polarizacin sur-norte, en relacin a Sol, es muy importante. En el diagrama de cajas se observa cmo la mayor dispersin se produce en la segunda y tercera coronas, en las que se localizan preferentemente los barrios con 5 y ms locales.</p>

      </section>
