      <section class="page__section page__section--text">

        <figure>
          <img src="/static/img/charts/mapa-extranjeros.jpg" alt="Barrios segn tasa de extranjeros">
          <figcaption>Barrios segn tasa de extranjeros. FRAVM.</figcaption>
        </figure>

        <chart-neighborhoods-by-foreigns-locals/>

        <figure>
          <img src="/static/img/charts/boxplot-extranjeros.png" alt="Barrios segn tasa de extranjeros" style="width:300px; height: auto;">
          <figcaption>Barrios segn tasa de extranjeros. FRAVM.</figcaption>
        </figure>

        <chart-neighborhoods-by-foreigns-locals-line/>

        <h4 ref="preciovivienda">Precio de las viviendas</h4>

        <p>Para medir la variable coste de instalacin se ha tomado la media del precio de venta por metro cuadrado de las viviendas a finales de 2018, segn el portal de Idealista.com. No se observa relacin definida de esta variable y el nmero de locales por barrios, pues el chi-cuadrado suma tan solo 15,36, con una p muy alta = 0,498. Por ello se obvia reproducir las tablas de contingencia y de residuos tpicos corregidos.</p>

        <figure>
          <img src="/static/img/charts/mapa-precios.jpg" alt="Barrios segn precio del metro cuadrado">
          <figcaption>Barrios segn precio del metro cuadrado. FRAVM.</figcaption>
        </figure>

        <chart-neighborhoods-by-price-locals/>

        <chart-neighborhoods-by-price-locals-line/>

        <!-- end determinantes -->


        <!-- start recopilacion -->
        <h3 ref="recopilacion">Recopilacin</h3>
        <p>Si se centra la atencin en las caractersticas de los espacios en que se localizan la mayora de los locales de juego y apuestas hay que subrayar su clarsima preferencia por los ejes comerciales distritales y mbitos de gran afluencia de pblico, bien comunicados.</p>
        <p>Dando un paso ms all, cabe interrogarse por las caractersticas socioeconmicas y demogrficas de los barrios en que se ubican. Entre la seccin censal, demasiado pequea, y el distrito, demasiado grande, la unidad geogrfica intermedia es el barrio. Sera bueno trabajar con barrios naturales, pero, al no existir informacin estadstica para ellos, hay que conformarse con los barrios administrativos.</p>
        <p>Pues bien, tenemos que los barrios en los que se localizan las casas de juego, preferentemente, se hallan dentro de la margen occidental de la almendra central y en los barrios perifricos del sur prximos a la M-30. En posiciones perifricas hay pocos locales. En general, son barrios de tamao medio y grande, medidos por el nmero de habitantes, y con ratios de densidad poblacional elevados. Parece, as pues, que los establecimientos buscan la proximidad de un tejido urbano muy consolidado, con actividad, saturado.</p>
        <p>Por otra parte, en general, son barrios con niveles de renta neta de las familias inferiores a la media municipal o en torno a ella. Dada la asociacin obvia entre el nivel de renta y el nivel de estudios de la poblacin, son barrios en los que el peso de los mayores de 25 aos sin estudios o con estudios acabados de bajo nivel es elevado. Lo mismo ocurre a propsito de la relacin entre la distribucin de los locales y la tasa de paro. Hay que llamar la atencin sobre la significativa asociacin existente entre el peso de la poblacin inmigrante y la distribucin espacial de las casas de juego.</p>
        <p>Son barrios con precios de las viviendas -euros por metro cuadrado- ms econmicos que en el resto de la ciudad, con la excepcin de los ms cntricos (distritos de Centro, Chamber y, en parte, Tetun).</p>
        <p>De tal modo, parece que los locales buscan zonas en las que la localizacin sea ms barata y, al tiempo, zonas en las que la poblacin residente tiene un nivel adquisitivo inferior a la media y un estatus social menos privilegiado, si bien por encima de los barrios ms vulnerables.</p>
        <p>Surge la pregunta: la eleccin de estos barrios, Ocurre porque los costes de instalacin -alquileres y precios de venta de los inmuebles- son ms asequibles o porque se trata de estar cerca de la clientela actual y potencial? O, ms bien, Inciden sendas dimensiones?</p>
        <p>Con la informacin emprica disponible no es fcil contestar. En realidad, los barrios con dotaciones amplias de establecimientos son minora:  el 70% de los locales estn en el 30% de los barrios. Hoy por hoy, es una dotacin con una cobertura territorial limitada. No son negocios de proximidad en sentido estricto. La lgica de implantacin no es la de, por as decirlo, una tienda de barrio.</p>
        <p>As las cosas, hemos de guardarnos de conclusiones precipitadas. El fenmeno de los locales de juego es reciente, probablemente no ha tocado techo. Las pautas de localizacin ahora conocidas dan pistas sobre el futuro previsible.</p>
        <p>As, encontramos muchos barrios con un perfil socioeconmico y demogrfico parecido al de los barrios con muchos locales que, sin embargo, carecen de ellos o que a lo sumo alojan 1-2. De ah que los anlisis de contingencia, as como los diagramas de dispersin, que se han realizado arriba muestran relaciones bivariadas significativas, pero con coeficientes que miden la relacin de dependencia moderados o incluso dbiles. Significativamente no se ha encontrado relacin alguna entre la dotacin de locales por barrios y los precios, a la vez que la relacin con la estructura de edades es muy leve.</p>
        <p>Abundando sobre la cuestin, en las pginas que siguen se aborda un anlisis de regresin mltiple. La variable dependiente es el nmero de locales por barrios y las variables explicativas son los indicadores que se han estudiado. Las correlaciones de Pearson de las diferentes variables con la dependiente son, en general, bajas o insignificantes. Mejoran algo si se toman las correlaciones parciales, esto es, si sobre cada asociacin bivariada se introduce una variable de control. Ambas correlaciones se recogen en la siguiente tabla: en la segunda columna estn las correlaciones de nmero de locales con las dems variables y en las columnas restantes estn las parciales de nmero de locales con las dems controlando por la variable que aparece en cabecera. Las primeras se leen en vertical y las segundas en horizontal. La estimacin de relaciones curvilneas (logartmicas, cbicas, cuadradas, inversas, logsticas) no ofrece mejores resultados.</p>

      </section>
    </div>

    <div class="page__wrap page__wrap--big">
      <section class="page__section page__section--text">
        <table-correlations/>
      </section>
    </div>

    <div class="page__wrap">
      <section class="page__section page__section--text">

        <p>Despus de ensayar diversos modelos de regresin, uno de los que mejores resultados arrojan es el que liga a la raz cuadrada del nmero de locales con las variables densidad, % de inmigrantes y poblacin. Los modelos alternativos examinados, por unas u otras razones, incumplen los criterios del anlisis de mnimos cuadrados<sup>5</sup>. La correlacin mltiple es alta (0,721), lo que significa que el 52% de la varianza est explicada. Las variables renta, distancia y precios no aparecen, cuando s lo hacen en los anlisis de contingencia y en las correlaciones bivariadas. Sucede que su influencia es canalizada por las tres variables introducidas en el modelo, con las que estn correlacionadas.</p>

        <div class="legend">
          <div class="legend__wrap">
            <sup>5</sup> El escogido cumple el criterio de normalidad de los residuos (Jarque-Bera = 4,93, con p = 0,085), el de no heterocedasticidad (contraste de heterocedasticidad de White = 13,49, inferior al valor crtico de 16,92) y el de no colinealidad (los factores de inflacin de la varianza de las tres variables son muy inferiores a 10). A su vez, el modelo est bien especificado, segn el contraste de Ramsey (F = 1,57 y p = 0,212, superior, pues, a 0.05). Todos los coeficientes son significativos individualmente (valores de t) y en conjunto (valor de F). 
          </div>
        </div>
      </section>
    </div>

    <div class="page__wrap page__wrap--big">
      <section class="page__section page__section--text">
        <table-regression/>
      </section>
    </div>

    <div class="page__wrap">
      <section class="page__section page__section--text">
        <!-- end recopilacion -->

        <!-- start othercities -->
        <h3 ref="othercities">La localizacin en otras grandes ciudades</h3>
        <p>A partir de las web de las 3 empresas ms importantes del sector (Sportium, Codere y Luckia) se han extrado las direcciones de sus locales en Espaa. La relacin no es completa. En particular, faltan muchos de los locales de Luckia. No obstante, la informacin es valiosa, al menos, a efectos ilustrativos. En el <router-link :to="{name:'map'}">mapa general</router-link> se aprecia la especial concentracin de los locales en la Comunidad de Madrid, Galicia, Euzkadi y el litoral mediterrneo.</p>
        <p>El descenso a las grandes capitales confirma la principal conclusin a que se ha llegado ms atrs: la concentracin en los distritos cntricos y la ausencia o escasa presencia en los distritos perifricos, en enclaves con claro carcter comercial, bien comunicados y con afluencia de pblico.</p>

        <h4 ref="barcelona">Barcelona</h4>
        <p>En Barcelona, la mayor concentracin se da en los distritos de Ciutat Vella y Eixample. Destacan calles como Va Laitena, travesera de Gracia, Gran Va de les Corts Catalanes y la avda. Diagonal.</p>

        <figure>
          <img src="/static/img/maps/barcelona.jpg" alt="Mapa de locales de Barcelona">
          <figcaption>Mapa de locales de Barcelona. Dadasign y FRAVM.</figcaption>
        </figure>

        <h4 ref="valencia">Valencia</h4>
        <p>En la capital valenciana, 8 distritos concentran el 60% de los locales detectados. De ellos destacan Rascanya, Quatro Carreres, Camins al Grau y la Saidia. No obstante, se observa una extensin-cobertura territorial muy superior a la vista en Madrid y Barcelona y a la que se ver en Sevilla, Mlaga, Zaragoza y Bilbao. Significativamente Ciutat Vella tiene pocos locales.</p>

        <figure>
          <img src="/static/img/maps/valencia.jpg" alt="Mapa de locales de Valencia">
          <figcaption>Mapa de locales de Valencia. Dadasign y FRAVM.</figcaption>
        </figure>

        <h4 ref="malaga">Mlaga</h4>
        <p>En Mlaga, ms de la mitad de los locales se localizan en los distritos de Centro y Carretera de Cdiz, con una especial concentracin sobre el eje longitudinal de la avda. Velzquez-Hroes de Sostoa.</p>

        <figure>
          <img src="/static/img/maps/malaga.jpg" alt="Mapa de locales de Mlaga">
          <figcaption>Mapa de locales de Mlaga. Dadasign y FRAVM.</figcaption>
        </figure>

        <h4 ref="sevilla">Sevilla</h4>
        <p>En Sevilla, las mayores concentraciones se producen al este del ro (distritos de Triana-Remedios, sobre el eje de la avda. Repblica Argentina) y en el distrito de Macarena (especialmente en el eje de la avda. Miraflores).</p>

        <figure>
          <img src="/static/img/maps/sevilla.jpg" alt="Mapa de locales de Sevilla">
          <figcaption>Mapa de locales de Sevilla. Dadasign y FRAVM.</figcaption>
        </figure>

        <h4 ref="zaragoza">Zaragoza</h4>
        <p>En Zaragoza se repite la concentracin en los distritos cntricos: Centro y Delicias, seguidos por Casco Histrico y Universidad.</p>

        <figure>
          <img src="/static/img/maps/zaragoza.jpg" alt="Mapa de locales de Zaragoza">
          <figcaption>Mapa de locales de Zaragoza. Dadasign y FRAVM.</figcaption>
        </figure>

        <h4 ref="bilbao">Bilbao</h4>
        <p>En Bilbao, la mayor concentracin tiene lugar en el distrito de Abando, en pleno centro de la ciudad, al que se suman los locales situados en el eje de la N-634, en el distrito de Recalde.</p>

        <figure>
          <img src="/static/img/maps/bilbao.jpg" alt="Mapa de locales de Bilbao">
          <figcaption>Mapa de locales de Bilbao. Dadasign y FRAVM.</figcaption>
        </figure>

        <p>Por ltimo, vemos que las casas de juego en la regin se distribuyen -adems de por Madrid- por los municipios del corredor del Henares y la zona sur metropolitana. Hay pocos en el oeste y norte. De tal modo, se hace notar la asociacin con las variables nivel de renta de las familias (rentas ms bajas, ms locales) y tasa de extranjera (proporciones ms altas, ms locales). En el interior de los diferentes municipios se repite la concentracin en las zonas centrales.</p>

        <figure>
          <img src="/static/img/maps/cmadrid.jpg" alt="Mapa de locales de la Comunidad de Madrid">
          <figcaption>Mapa de locales de la Comunidad de Madrid. Dadasign y FRAVM.</figcaption>
        </figure>

        <figure>
          <img src="/static/img/maps/metromadrid.jpg" alt="Mapa de locales del rea metropolitana de Madrid">
          <figcaption>Mapa de locales del rea metropolitana de Madrid. Dadasign y FRAVM.</figcaption>
        </figure>
        <!-- end othercities -->

        <!-- start educationcenters -->
        <h3 ref="educationcenters">Cercana a los institutos pblicos</h3>
        <p>Uno de los aspectos de los locales de juego y las casas de apuestas que ms preocupa a la ciudadana es la cercana de stas a los institutos. El Real Decreto 42/2019 del Consejo de Gobierno de la Comunidad de Madrid da nueva redaccin al artculo 34 del Reglamento de Juego, regulando que cualquier establecimiento ha de estar a una distancia no inferior a 100 metros respecto de los colegios en que se impartan enseanzas regladas y obligatorias (primaria o secundaria). A su vez, la disposicin transitoria sexta dice que los locales que estn dentro de ese radio disponen de un periodo de 10 aos para adaptarse.</p>

        <p>Segn los datos del IECM, en la ciudad hay 863 colegios pblicos y privados que imparten primaria y/o secundaria. En esa cifra se incluyen, adems de los colegios, los institutos, los centros de educacin especial y los centros extranjeros en Espaa. Identificamos 53 colegios con locales dentro del radio de los 100 metros, un 6,1% del total de centros. De ellos, 30 son privados y 23 pblicos<sup>6</sup>. En el siguiente cuadro se ofrece el detalle de los colegios afectados. Los locales mal colocados suman 61.</p>

        <div class="legend">
          <div class="legend__wrap">
            <sup>6</sup> Estas cifras las tomamos como una estimacin aproximada. El dato real variar algo hacia arriba, en tanto es probable que el universo de locales que hemos reconstruido est incompleto.
          </div>
        </div>

        <table-locals-by-educationcenters/>

        <p>A continuacin se incluyen tres mapas del mayor inters. El primero arroja una vista panormica de la confluencia de los locales y los colegios. Es muy significativo el contraste entre la amplia cobertura territorial de los centros educativos y la concentracin de los locales y salones. La superposicin de radios entre sendas redes se produce en el mbito de stos. En el segundo mapa  se recoge, a modo de ejemplo, un colegio que tiene en su entorno inmediato tres establecimientos. Ntese cmo dos de ellos estn a bastante menos de 100 metros. En el tercero mapa, por ltimo, se concretan los colegios en cuyo radio de 100 metros a la redonda hay uno o ms establecimientos. Centro, Puente de Vallecas y Latina son los tres distritos con ms colegios afectados.</p>

        <figure>
          <img src="/static/img/maps/localescolegios.jpg" alt="Mapa de locales y colegios de la Comunidad de Madrid">
          <figcaption>Mapa de locales y colegios de la Comunidad de Madrid. FRAVM.</figcaption>
        </figure>

        <figure>
          <img src="/static/img/maps/localescolegiosarea.jpg" alt="Colegio con 3 locales en menos de 100m">
          <figcaption>Colegio con 3 locales en menos de 100m. FRAVM.</figcaption>
        </figure>

        <figure>
          <img src="/static/img/maps/localescolegiosmadrid.jpg" alt="Colegios con alun local a menos de 100m">
          <figcaption>Colegios con alun local a menos de 100m. FRAVM.</figcaption>
        </figure>

        <p>Aunque la normativa vigente fija una distancia mnima de 100 metros de cada local y saln a un colegio, es indudable que el caso ms preocupante y sobre el que merece la pena enfatizar la atencin es el de la distancia respecto de los centros de secundaria. La regla de los 100 metros parece muy tmida. En el siguiente cuadro se estima el nmero de locales y salones que tienen algn colegio dentro de su rea considerando varios radios.</p>

        <chart-locals-by-educationcenters/>
        <!-- end educationcenters -->

      </section>
    </div>
  </div>
</template>

<script>
export default {
  name: 'report-page',
  created: function(){
    window.scrollTo(0,0);
  },
  methods: {
    scrollTo(refName) {
      let element = this.$refs[refName];
      window.scrollTo(0, element.offsetTop - 60);
    }
  }
}

</script>


