        <p>Antes de la aprobacin del <a href="http://www.bocm.es/boletin/CM_Orden_BOCM/2019/05/17/BOCM-20190517-2.PDF">Decreto 42/2019</a>, la FRAVM y otras organizaciones<sup>1</sup> suscribieron un <a href="https://aavvmadrid.org/noticias/propuestas-urgentes-en-materia-de-locales-de-juego-y-apuestas/">documento de propuestas</a>. Aqu nos hacemos eco de ellas, introduciendo algunas novedades determinadas por la aparicin de nuevas circunstancias, entre ellas el propio Decreto.</p>

