        <!-- start comunidad -->
        <h3 ref="comunidad">Para la Comunidad de Madrid</h3>
        <ul>
          <li>1. <strong>Impulsar la creacin de Zonas de Especial Proteccin de Poblacin Vulnerable</strong>, con el objetivo de reducir al mximo en ellas el nmero de licencias de locales de apuestas y de juego y alejar el fantasma de la ludopata de personas con bajo nivel de ingresos y escasos recursos. El modelo puede ser similar al de la implantacin de las actuales Zonas de Proteccin Acstica Especial (ZPAE).</li>
          <li>2. <strong>Aprobar un plan especial de inspeccin</strong> que aumente la periodicidad de las inspecciones pblicas con objeto de detectar y sancionar ilegalidades y hacer efectiva la prohibicin de acceso a estos establecimientos de menores y personas inscritas en el Registro General de Interdicciones de Acceso al Juego (RGIAJ). Mayor coordinacin entre las policas nacional y municipal en estas labores. La operacin policial realizada en toda Espaa los das 27 y 28 de septiembre de 2019 ha demostrado numerosos incumplimientos por parte de los establecimientos en esta materia.</li>
