          <li>9. <strong>Dotar al Servicio de Control de Juegos de Azar de un nmero de efectivos</strong> (inspectores, subinspectores y agentes) proporcional a los indicadores de crecimiento de estos locales.</li>
          <li>10. <strong>Crear un Consejo del Juego de la Comunidad de Madrid</strong> en el que estn representadas las distintas administraciones pblicas, los operadores del juego y las asociaciones de afectados/as.</li>
