        <ul>
          <li>1. <strong>Impulsar la creacin de Zonas de Especial Proteccin de Poblacin Vulnerable</strong>, con el objetivo de reducir al mximo en ellas el nmero de licencias de locales de apuestas y de juego y alejar el fantasma de la ludopata de personas con bajo nivel de ingresos y escasos recursos. El modelo puede ser similar al de la implantacin de las actuales Zonas de Proteccin Acstica Especial (ZPAE). El artculo 35 de la Ley del Suelo de la Comunidad de Madrid establece como una determinacin pormenorizada de la ordenacin urbanstica el rgimen normativo de usos pormenorizados e intervenciones admisibles y prohibidas, as como las condiciones que deben cumplir para ser autorizadas. A su vez, el artculo 38 de la misma dice que es una determinacin pormenorizada la regulacin de las condiciones de admisibilidad de cada uno de los distintos usos pormenorizados en la delimitacin de suelo correspondiente.</li>
